*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
}

body{
  width: 100%;
  height: 100%;
  background-color: #000;
  font-family: sans-serif;
}

a{
  text-decoration: none;
  color: #fff;
}

h1,h2,h3,h4,h5,h6,span{
  font-size: 18px;
  color: #fff;
  font-family: Poppins, sans-serif;
}

li{
  overflow: hidden;
}

p{
  font-size: 16px;
  font-family: Poppins, sans-serif;
}

.container{
  width: 80%;
  margin: 0px auto;
}

.white{
  color: #fff !important;
}

.d_flex{
  display: flex;
}

.row{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  overflow: hidden;
  align-items: flex-start;
  justify-content: center;
}

.col_2{
  max-width: 45%;
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
}
.component_space{
  padding: 100px 0;
}

.pointer{
  cursor: pointer;
}

button.btn{
  font-size: 18px;
  font-weight: 500;
  border-radius: 5px;
  border: 2px solid #f9004d;
  background-color: #f9004d;
  color: #fff !important;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: .4s all ease;
  height: 50px;
  width: 180px;
  margin: 0 10px;
}

.p_color{
  color: rgba(198, 201, 216, .75);
  font-size: 18px;
  line-height: 30px;
  font-family: Poppins, sans-serif;
  margin-bottom: 15px;
}
button.about.btn.pointer{
  border: none;
  background-image: linear-gradient(90deg,#0924ef, #c010f6 );
  transition: .4s all ease;

}
button.about.btn.pointer:hover{
  transform: translateY(5px);
    background-image: linear-gradient(90deg, #c010f6, #0924ef);
}

@media only screen and (max-width:868px) {
  .col_2{
    max-width: 100%;
  }
}

.col_3{
  display: flex;
  padding: 20px;
  justify-content: center;
  flex-direction: column;
  margin-top: 20px;
}